import "../Profile.css";
import penIcon from "../pen.png";
import React, { useState, useEffect } from 'react';
import NavigationBar from './HomePage.js'
import userService from "../service/userService.js";


function Pfp(){
    const initialState = {
        first_name: '',
        last_name: '',
        email: '',
        phone: '',
        profile_picture: null,
        cv: null,
        work_experience: '',
        education: '',
        skills: ''
    };

    const [user, setUser] = useState(initialState);

    useEffect(() => {
        const fetchUserData = async () => {
            try {
                const data = await userService.getUserData(localStorage.getItem('userID'));
                setUser((user) => ({
                    ...user,
                    ...data // merge fetched data into the existing user state
                }));
            } catch (error) {
                console.error('Error fetching user data:', error);
            }
        };

        fetchUserData();
    }, []);

    console.log(user.profile_picture);

    const base64Image = `data:image/jpeg;base64,${user.profile_picture}`;

    return(
        <div className="banner">
            <div className="profile-picture-container">
                <img
                    src={base64Image}
                    alt="Profile"
                    //className="profile-picture"
                />
            <div
                className="edit-icon"
                title="Edit Profile Picture"
                // onClick={() => handleEditClick("Profile Picture")}
            >
                <img
                    src={penIcon}
                    alt="Pen"
                    className="pen-icon"
                />
            </div>
            </div>
        </div>
    );
}

function Profile(){
    return(
        <div>
            <NavigationBar></NavigationBar>
            <br></br>
            <Pfp></Pfp>
        </div>
    );
}

export default Profile;